@font-face {
  font-family: AreYouOkay;
  src: url(/portfolio/static/media/AreYouOkay.bea626ec.ttf); }

body {
  font-family: "Titillium Web", sans-serif; }

header {
  background-color: #B54A5D;
  width: 100%;
  height: 100px;
  position: absolute;
  margin-top: 20px;
  z-index: 100;
  color: white;
  box-shadow: 0 5px 10px 1px grey, 0 -5px 10px 1px grey; }
  header div {
    font-family: AreYouOkay, "Courier New";
    padding: 5px 0 0 32px;
    font-size: 25px; }
  header span {
    padding: 5px 50px;
    font-size: 16px; }

.subcontainer {
  opacity: 0;
  -webkit-transition: opacity 1s;
  transition: opacity 1s; }
  .subcontainer .box {
    width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 5px 5px 15px 1px grey;
    text-align: center; }
    .subcontainer .box a {
      color: #4ab5a2; }
  .subcontainer h3 {
    font-family: AreYouOkay, "Courier New";
    color: #B54A5D; }
  .subcontainer h4 a {
    color: #4ab5a2; }
  .subcontainer .tags div {
    margin: 0 5px;
    padding: 0 5px;
    border-radius: 20px;
    background-color: #4ab5a2;
    font-size: 12px;
    color: white; }
  .subcontainer ul {
    list-style-type: none;
    padding-left: 0; }
  .subcontainer .items {
    position: relative;
    width: 60%;
    margin: auto;
    margin-top: 2px;
    padding: 5px;
    font-weight: bold;
    -webkit-transition: all 2s;
    transition: all 2s;
    color: white;
    background-color: #4ab5a2;
    border-radius: 5px; }
  .subcontainer .hidden {
    top: -1000px; }
  .subcontainer .slideDown {
    -webkit-animation: drop 1s;
            animation: drop 1s; }

.fadeIn {
  opacity: 1; }

.projects {
  padding-top: 130px; }

@-webkit-keyframes drop {
  0% {
    top: -1000px;
    color: black;
    background-color: rgba(255, 255, 255, 0); }
  80% {
    color: black;
    background-color: rgba(255, 255, 255, 0);
    width: 50%; }
  100% {
    top: 0; } }

@keyframes drop {
  0% {
    top: -1000px;
    color: black;
    background-color: rgba(255, 255, 255, 0); }
  80% {
    color: black;
    background-color: rgba(255, 255, 255, 0);
    width: 50%; }
  100% {
    top: 0; } }

@media screen and (min-width: 768px) {
  .subcontainer {
    height: 100%;
    max-height: 100vh;
    overflow: hidden;
    padding-top: 130px; }
  .arrows {
    font-size: 130px !important; }
  #title {
    font-size: 40px; } }

@media (hover: hover) {
  #slider {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    margin-top: 50px !important; } }

.arrows {
  font-size: 50px;
  color: #B54A5D;
  text-shadow: 5px 5px 5px grey;
  -webkit-transition: text-shadow 200ms;
  transition: text-shadow 200ms; }
  .arrows:hover {
    cursor: pointer; }
  .arrows:active {
    text-shadow: none; }

@-webkit-keyframes animate {
  50% {
    box-shadow: 5px 5px 5px #B54A5D; } }

@keyframes animate {
  50% {
    box-shadow: 5px 5px 5px #B54A5D; } }

.chevron:nth-child(1)::before {
  -webkit-animation-delay: -200ms;
          animation-delay: -200ms; }

.chevron:nth-child(2)::before {
  -webkit-animation-delay: -400ms;
          animation-delay: -400ms; }

.chevron:nth-child(3)::before {
  -webkit-animation-delay: -600ms;
          animation-delay: -600ms; }

.chevron:nth-child(4)::before {
  -webkit-animation-delay: -800ms;
          animation-delay: -800ms; }

.chevron:nth-child(5)::before {
  -webkit-animation-delay: -1000ms;
          animation-delay: -1000ms; }

.chevron::before {
  display: block;
  position: relative;
  top: 50px;
  content: "";
  height: 100px;
  width: 100px;
  margin-top: -60px;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
  -webkit-animation: animate 2s infinite;
          animation: animate 2s infinite; }

#slider {
  margin-top: 100px; }

.note {
  font-size: 12px; }


